.advantages {
  background-color: #fff5f6;

  &__list {
    width: 230px;
    margin-top: -38px;
    @media screen and (min-width: 768px) {
      display: flex;
      width: auto;
      margin-left: -60px;
    }
    @media screen and (min-width: 1280px) {
      margin-left: -30px;
    }
  }

  %pseudoIcon {
    content: '';
    display: block;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    margin-bottom: 16px;
  }

  &__item {
    margin-top: 38px;
    &:nth-child(1)::before {
      background-image: url(/images/advantages/udder.png);
      @extend %pseudoIcon;
    }
    &:nth-child(2)::before {
      background-image: url(/images/advantages/apple.png);
      @extend %pseudoIcon;
    }
    &:nth-child(3)::before {
      background-image: url(/images/advantages/icecream.png);
      @extend %pseudoIcon;
    }

    @media screen and (min-width: 768px) {
      width: 180px;
      margin-left: 60px;
      &:nth-child(n)::before {
        margin-bottom: 36px !important;
      }
    }
    @media screen and (min-width: 1280px) {
      width: 330px;
      margin-left: 30px;
      &:nth-child(n)::before {
        margin-bottom: 40px !important;
      }
    }
  }

  &__facts {
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 36px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $first-accent-color;
    margin-bottom: 15px;

    @media screen and (min-width: 768px) {
      font-size: 48px;
      line-height: 1.15;

      text-transform: lowercase;
    }
    @media screen and (min-width: 1280px) {
      font-size: 58px;
      line-height: 1.14;

      margin-bottom: 13px;
    }
  }

  &__text {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0.02em;
    color: $title-color;
    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.71;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.75;
    }
  }
}
