.logo{
  width: 200px;
  height: 50px;
  margin-right: auto;

  &__img{
    width: 100%;
    height: 100%;
  }

}

.header{
  position: relative;
  min-height: 502px;
  z-index: 1;
  overflow: hidden;
  padding: 6px 20px;
  background-color: #FFB8CA;

  @media screen and (min-width: 768px){
      padding-top: 37px;
      min-height: 400px;
  }

  @media screen and (min-width: 1280px)  {
      padding-left: 46px; 
      min-height: 665px;
  }
  
&__container{
  display: flex;
  align-items: center;
}
&__nav-items{
  display: none;
  @media screen and (min-width: 1280px){
  display: flex;
  align-items: center;
  }

}
&__nav-item:not(:last-child){
  margin-right: 0;
  margin-bottom: 0;
  @media screen and (min-width: 1280px){
      margin-right: 35px;
  }
  
}
&__nav-link{
  padding: 10px 0;

  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: 0em;
  
  color: $primary-white-color;
  transform: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

}
&__nav-link:hover,
&__nav-link:focus{
  color: $first-accent-color;
}

&__nav-link--color{
  padding: 10px 0;

  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: 0em;
  color: #C7133E;
  transform: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

}
&__nav-link--color:hover,
&__nav-link--color:focus{
  color: $first-accent-color;
}

&__button-now{
  display: none;
  background-color: #fff;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: center;
  color: #D41443;
  
  transform: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px){
      display: block;
  }

  @media screen and (min-width: 1280px){
      margin-left: auto;
      margin-right: auto;
  }

}
&__button-now:hover,
&__button-now:focus{
  border-radius: 22px;
  background-color: #ECEBEB;
  color: #D41443;
  box-shadow: 1px 3px 8px 1px #00000029;
  box-shadow: 0px 3px 3px 1px #0000000f;
  box-shadow: 0px 1px 3px 1px #0000001f;
  }

  &__table-menu-button-now{
    display: block;
    background-color: #fff;
    font-family: DM Sans;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3;
    letter-spacing: 0em;
    text-align: center;
    color: #D41443;
    
    transform: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

&__button-menu{
  display: block;
  @extend %centeredFlex;
  border: 0;
  background-color: transparent;
  margin-left: auto;
  @media screen and (min-width: 768px){
      margin-right: 30px;
  }

  @media screen and (min-width: 1280px){
      display: none;
  }
  
}

&__table-menu{
  transform: translateX(200%);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  position: absolute;
  z-index: 20;
  width: 254px;
  right: 0;
  top: 0;
  
  display: flex;
  flex-direction: column;
  padding: 60px 34px ;

  &.is-open{
    transform: translateX(0);
  }

  background-color: #E17992;
  @media screen and (min-width: 768px){
      height: 1020px;
  }

  @media screen and (min-width: 1280px){
      display: none;
  }
  
}

&__button-close{
  position: absolute;
  top: 24px;
  right: 34px;
  @extend %centeredFlex;
  width: 25px;
  height: 25px;
  border: 0;
  background-color: #E17992;;
  
  
}
&__nav-items-menu{
  margin-top: 32px;
}


&__title-thumb{
  position: absolute;
  top: 193px;
  left: 20px;

  display: block;
  text-align: left;
  
  @media screen and (min-width: 768px){
      top: 102px;
      left: 34px;
      width: 300px;
  }

  @media screen and (min-width: 1280px){
      width: 277px;
      top: 171px;
      left: 115px;
  }
  
}
&__title{
  margin-bottom: 20px;
  z-index: 2;

  font-family: Titan One;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-align: left;
  text-transform: uppercase;

  color: $primary-white-color;
  @media screen and (min-width: 768px){
      font-size: 22px;
  }

  @media screen and (min-width: 1280px){
      font-size: 38px;
      margin-bottom: 25px;
  }
}

&__title-red{
  font-size: 48px;
  color:  #D41443;

  @media screen and (min-width: 768px){
      font-size: 34px;
  }

  @media screen and (min-width: 1280px){
      font-size: 58px;
  }

}
&__title-buttons{
  display: flex;
}
&__title-button:not(:last-child){
  margin-right: 17px;
}
&__title-button{
  display: flex;
  align-items: center;
  height: 24px;
  min-width: auto;
  padding: 0 20px;
  
  font-family: DM Sans;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: center;


  color: $primary-white-color;
  background-color: $first-accent-color;
  transform: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  @media screen and (min-width: 1280px){
      height: 40px;
      font-size: 14px;
  }

}


&__title-button:hover,
&__title-button:focus{
  background-color: #ECEBEB;;
  color: #D41443;
  box-shadow: 1px 3px 8px 1px #00000029;
  box-shadow: 0px 3px 3px 1px #0000000f;
  box-shadow: 0px 1px 3px 1px #0000001f;

}

&__title-button-second {
  display: flex;
}
&__title-button-second:not(:last-child){
  margin-right: 17px;
}
&__title-button-second{
  display: flex;
  align-items: center;
  height: 24px;
  min-width: auto;
  padding: 0 20px;
  
  font-family: DM Sans;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: center;

  
  color: $first-accent-color;
  background-color: $primary-white-color;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
                              color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  @media screen and (min-width: 768px){
    width: 96px;
  }
  @media screen and (min-width: 1280px){
      height: 40px;
      font-size: 14px;
      width: 140px;
  }
  

}


&__title-button-second:hover,
&__title-button-second:focus{
  background-color: #ECEBEB;;
  color: #D41443;
  box-shadow: 1px 3px 8px 1px #00000029;
  box-shadow: 0px 3px 3px 1px #0000000f;
  box-shadow: 0px 1px 3px 1px #0000001f;

}

&__img-milk-thumb{
  display: none;
  position: absolute;
  top: 245px;
  left: 34px;

  height: 126px;
  width: 124px;
  @media screen and (min-width: 768px){
      display: block;
  }

  @media screen and (min-width: 1280px){
      top: 429px;
      left: 77px;
      height: 191px;
      width: 188px;
  }


}

&__text-milk-wrap{
  position: absolute;
  left: 154px;
  top: 292px;

  display: block;
  width: 110px;
  height: 48px;

  overflow: hidden;
  

  @media screen and (min-width: 1280px){
      width: 200px;
      height: 78px;
      left: 295px;
      top: 485px;}

}
&__text-milk{
  display: none;
  font-family: DM Sans;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0em;
  text-align: left;

  color: $primary-white-color;
  @media screen and (min-width: 768px){
      display: block;
  }

  @media screen and (min-width: 1280px){
      font-size: 14px;
  }

}
&__milk-button{
  display: none;
  position: absolute;
  left: 154px;
  top: 347px;

  @extend %centeredFlex;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 0;
  @media screen and (min-width: 768px){
      display: flex;
  }
  @media screen and (min-width: 1280px){
      left: 295px;
      top: 583px;
      width: 26px;
      height: 26px;
  }

}
&__milk-button:focus,
&__milk-button:hover{
  cursor: pointer;
  box-shadow: 1px 3px 8px 1px #00000029;
  box-shadow: 0px 3px 3px 1px #0000000f;
  box-shadow: 0px 1px 3px 1px #0000001f;
}
&__button-svg{
  color: $first-accent-color
}

&__milk-button:active &__button-svg{
  color: black
}


&__big-icecream{
  position: absolute;
  left: 117px;
  top: 92px;

  z-index: -1;
  width: 171px;
  height: 410px;
  @media screen and (min-width: 768px){
      left: 378px;
      top: 63px;
      width: 143px;
      height: 336px;
  }
  @media screen and (min-width: 1280px){
      width: 236px;
      height: 557px;
      left: 594px;
      top: 108px;
      
  }
}

&__circle{
  position: absolute;
  left: 36px;
  top: 83px;

  z-index: -2;

  display: block;
  height: 322px;
  width: 322px;
  border-radius: 50%;
  background-color:  #FFA5BA;
  @media screen and (min-width: 768px){
      height: 322px;
      width: 322px;
      left: 307px;
      top: 31px;

  }
  @media screen and (min-width: 1280px){
      height: 538px;
      width: 538px;
      left: 475px;
      top: 52px;

  }

}
&__right-text-container{
  display: none;
  position: absolute;
  right: 44px;
  top: 94px;
  @media screen and (min-width: 768px){
      display: block;
  }

  @media screen and (min-width: 1280px){
      right: 115px;
      top: 160px;
  }

}

&__right-text-thumb{
  display: block;
}
&__right-text-number{
  position: relative;

  z-index: 1;
  font-family: Titan One;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-align: right;
  color:  #D41443;


  @media screen and (min-width: 1280px){
      font-size: 46px;
  }


}
&__right-text-desk{
  margin-bottom: 24px;
  
  font-family: DM Sans;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: right;

  color: $primary-white-color;

  @media screen and (min-width: 1280px){
      margin-bottom: 18px;
      font-size: 14px;
  }

}
&__right-text-circle{
  position: absolute;
  top: -2px;
  right: -10px;
  z-index: -1;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: $primary-white-color;
  @media screen and (min-width: 1280px){
      top: -4px;
      right: -18px;
      width: 30px;
      height: 30px;
  }
}

&__so-tasty-img-thumb{
  display: none;
  position: absolute;
  top:130%;
  right: 34px;

  width: 168px;
  height: 133px;
  @media screen and (min-width: 768px){
      display: block;
  }
  @media screen and (min-width: 1280px){
      top:273px;
      right: 33px;

      width: 293px;
      height: 232px;
  }
}
  
}